@import url('https://rsms.me/inter/inter.css');
html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

a {
  color: black !important;
}

h1 {
  font-size: 1rem;
  font-weight: 600;
}

.strong {
  font-weight: 600;
}

small {
  color: #b1b2b3;
}

.float-right {
  float: right;
}

.right-floated-spacer {
  margin-left: 8px;
  margin-bottom: 8px;
}

.contentContainer {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  margin: 48px;

  flex-flow: row wrap;
  justify-content: space-around;
}

.contentContainer .sideNav {
  width: 200px;
  margin-bottom: 48px;
  text-align: left;
}

.contentContainer .mainContent {
  flex-grow: 1;
  min-width: 300px;
  max-width: 800px;

  text-align: left;
}

.sideNav ul {
  padding-left: 0;
}

.sideNav li {
  text-decoration: none;
  list-style: none;
}

.sideNav img.logoImg {
  max-width: 20px;
  position: relative;
  top: -2px;
}


.leftAligned {
  text-align: left;
}
.cleanInputContainer {
  margin-top: 24px;
}
img.logoImg {
  max-width: 275px;
}

[contenteditable].cleanInput {
  display: inline-block;
  min-width: 200px;
  outline: 0px solid transparent;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-359deg);
  }
}

.rotate {
  animation: rotation 7s infinite linear;
}

.basic-button {
  border: 1px solid black;
  display: inline-block;
  width: 240px;
  cursor: pointer;
  margin: 24px;
  text-align: center;
}
.basic-button:hover {
  cursor: pointer;
}

.basic-button.disabled:hover {
  cursor: default;
}

body {
    background-color: white;
    //font-family: "VT323";
    font-family: "Inter", "Inter UI", -apple-system, sans-serif;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    line-height: 1.666666;
    -webkit-text-size-adjust: 100%;
}

.flexbox {
    display: flex;
    justify-content: center;
}

.centered { 
    text-align: center;
    position: fixed;
    top: 38%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 550px;
}

.centered-text {
  text-align: center;
}

.extra-margin {
  margin: 24px auto;
}

.no-margin {
  margin: 0;
}

.small-margin {
  margin: 4px 0 18px 0;
}

.extra-wide {
  min-width: 600px !important;
}

.right-fixed {
  width: 150px;
  display: inline-block;
  text-align: right;
}
.voteList {
}
.voteDisplayAddress {
  line-height:0.8;
}
.voteDisplayAmount {
  display: block;
  line-height:1;
  margin-bottom: 12px;
}

.content-centered {
  width: 800px;
  margin: 200px auto 0 auto;
  text-align: center;
}
.content-centered .left-column {
  display: inline-block;
  padding-right: 8px;
  width: 300px;
  text-align: right;
}
.content-centered .right-column {
  display: inline-block;
  padding-left: 8px;
  width: 300px;
  text-align: left;
}
.content-centered .center-row {
  height: 36px;
}
